<template>
  <div class="container">
    <AuthorTutorial></AuthorTutorial>
    <div class="left-column">
      <div class="mindmap-container">
        <div v-html="mindMap" @click="handleWordClick"></div>
      </div>
    </div>
    <div class="right-column" v-if="showWriteArea">
      <div class="author">
        <div style="display: flex; align-items: center;">
          <label>Story Name </label>
          &emsp;
          <input v-model="inputTitle">
          <span style="flex-grow: 1;"></span>
          <button class="button" @click="importFile()">Import Story </button>
        </div>
        <input type="file" id="file-input" style="display: none" @change="onFileInputChange">
        <div class="para-container">
          <p class="preserve-line-breaks" @click="handleWordClick">{{ para }}</p>
        </div>
        <div>
          <div>
            <textarea v-model="mindText" v-if="developer"></textarea>
            <button v-on:click="generateMindMap" v-if="developer">generate MindMap</button>
          </div>
          <textarea class="responsive-textarea" v-model="inputText" placeholder="write story here but leave blank if go to existing node"></textarea>

          <div v-if="steps.step1">
            <label>From para index (behind sentence): </label>
            <input v-model="paraIndexFrom" />
            <br />
            <label>From the para word (click) "{{ wordIndex }}".</label>
            <input v-model="wordIndex" />
            <div class="centered-flex">
              <p style="display: inline;">Click&emsp;</p>
              <button @click="step1Finish" style="background-color: red; color: white; display: inline;">next</button>
              <p style="display: inline;">&emsp; to set what para you want to go to:</p>
            </div>
          </div>
          <br />
          <div v-if="steps.step2">
            <label>What para index go to: </label>
            <input v-model="paraIndexGoTo" />
            <div>
              <p>The default option is to create a new paragraph</p>
              <p>When you try to link to an existing chapter, the above story will be invalid because you cannot modify the existing chapter content</p>
              <p>When you finished, click "Add Para" below to add to the story</p>
            </div>
            <div class="centered-flex">
              <p style="display: inline;">Or you can click&emsp;</p>
              <button @click="step1Back" style="background-color: red; color: white; display: inline;">back</button>
              <p style="display: inline;">&emsp; to the first step</p>
            </div>
          </div>
          <br /><br />
          <!-- @=v-on -->
          <button @click="addPara">Add Para</button>
          <br /><br />
          <button @click="uploadText">Upload Story Online</button>
          &emsp;&emsp;&emsp;
          <button @click="downloadText">Download Story Locally</button>
        </div>
      </div>
    </div>
    <button class="write-hidden-btn" v-show="!showWriteArea" @click="toggleWriteArea">
      <img src="../assets/header/back-arrow-4845.png" alt="Enpand" class="icon-toggle">
    </button>
    <button class="write-hidden-btn" v-show="showWriteArea" @click="toggleWriteArea">
      <img src="../assets/header/arrow-203.png" alt="Indent" class="icon-toggle">
    </button>
  </div>
</template>

<script >
import { saveAs } from 'file-saver'
import mermaid from 'mermaid';
import { useNovelStore } from '../stores/novelStore'
import { useStore } from '../stores/store'
import { useUserStore } from '../stores/userStore';
import { getStorage, ref, uploadBytesResumable, getDownloadURL } from "firebase/storage";
import { app } from "../../firebaseConfig";
import AuthorTutorial from '../components/tutorials/AuthorTutorial.vue';

export default {
  components: {
    AuthorTutorial,
  },
  setup() {
    const store = useStore();
    const novelStore = useNovelStore();
    const user = useUserStore();
    return { user, store, novelStore };
  },
  data() {
    return {
      developer: false,
      step: 1,
      inputTitle: 'None',
      wordIndex: ' ',
      paraIndexFrom: 0,
      paraIndexGoTo: 1,
      goBack: false,
      para: ' ',
      inputText: '',
      allStory: '',
      paragraphs: [],
      index: 0,
      paraList: ["Story Begin"],
      paraMindList: ["Story Begin [0]"],
      text: '',
      graph: '',
      mindText: '0["Story Begin"]',
      mindMap: '',
      alpha: '',
      steps: {
        step1: true,
        step2: false
      },
      novel: {
        title: '',
        author: '',
        content: ''
      },
      showWriteArea: true
    }
  },
  created() {
    this.fromCommunity();
  },
  methods: {
    fromCommunity() {
      if (this.$route.query.startReading) {
        // Start reading automatically
        //console.log(this.novelStore.currentNovel);
        if (this.novelStore.currentNovel != null) {
          this.parseNovelText(this.novelStore.currentNovel);
          //console.log(this.novelStore.currentNovel);
        } else {
          alert("Please go to the community interface to select the book")
        }
      }
      return null;
    },
    step1Finish() {
      this.steps.step1 = false;
      this.steps.step2 = true;
    },
    step1Back() {
      this.steps.step1 = true;
      this.steps.step2 = false;
    },
    uploadText() {
      var blob = new Blob(["Story Title: " + this.inputTitle + '\n' + this.allStory], {
        type: "text/plain"
      });
      this.uploadTextFileToFirebase(blob, this.inputTitle + ".txt");
    },
    async uploadTextFileToFirebase(file, title) {
      const storage = getStorage(app);
      const storageRef = ref(storage, `Novels/${title}`);
      const token = this.user.userInfo.token;
      const uploadTask = uploadBytesResumable(storageRef, file, {
        // 
        customMetadata: {
          'firebaseAuth': token,
        }
      });
      uploadTask.on('state_changed',
        (snapshot) => {
          // Observe state change events such as progress, pause, and resume
          // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
          const progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
          console.log('Upload is ' + progress + '% done');
          switch (snapshot.state) {
            case 'paused':
              console.log('Upload is paused');
              break;
            case 'running':
              console.log('Upload is running');
              break;
            default:
              break;
          }
        },
        (error) => {
          // Handle unsuccessful uploads
          console.error('Upload failed:', error);
          alert("Uploading to the server failed, please check if you are logged in")
        },
        async () => {
          // Handle successful uploads on complete
          const downloadURL = await getDownloadURL(storageRef);
          alert('File available online, you can move community tab to check', downloadURL);
          // Call a function to handle the file URL, e.g., save it in your database
          // handleFileURL(downloadURL);
        }
      );
    },

    toggleWriteArea() {
      this.showWriteArea = !this.showWriteArea;
    },
    importFile() {
      document.getElementById('file-input').click();
    },
    onFileInputChange(event) {
      const file = event.target.files[0];
      const reader = new FileReader();
      reader.onload = (event) => {
        const text = event.target.result;
        this.parseNovelText(text);
      };
      reader.readAsText(file);
    },
    parseNovelText(text) {
      const lines = text.trim().split('\n');
      let title = '';
      let author = '';
      let content = '';
      for (let line of lines) {
        if (line.startsWith('Story Title: ')) {
          title = line.slice(13);
        } else if (line.startsWith('Authors: ')) {
          author = line.slice(9);
        } else {
          content += line + '\n';
        }
      }
      this.novel.title = title;
      this.inputTitle = title;
      this.novel.author = author;
      this.novel.content = content;
      const nodes = content.split('\n');

      nodes.forEach(paragraphText => {
        const matches = paragraphText.match(/\[(\d+)\] \[(.*?)\] (.*) \[(\d+)\]/);
        if (matches) {
          const sourceIndex = parseInt(matches[1]);
          const wordIndex = matches[2];
          const content = matches[3].replaceAll('"', "'");
          const paragraphIndex = parseInt(matches[4]);

          this.paraIndexFrom = sourceIndex;
          if(wordIndex === ""){
            this.wordIndex = " ";
          } else {
            this.wordIndex = wordIndex;
          }
          this.inputText = content;
          this.paraIndexGoTo = paragraphIndex;
          this.addPara(false);
        }
      });
      this.generateMindMap();
    },


    addPara(updateMindMap = true) {
      if (this.paraIndexFrom > this.index || this.paraIndexGoTo > this.index + 1) {
        alert("The paragraph that matches the subscript does not exist or is skipped, please check from the subscript or Go to subscript");
      } else if (this.paraIndexGoTo === 0) {
        alert("the beginning node is 1");
      } else if (this.wordIndex.trim() === '' && this.index != 0) {
        alert("Please select/fill word in 'From the para word (click) '")
      } else {
        if (this.paraIndexGoTo <= this.index) {
          alert("when go to the exist node, the story content will be deleted")
          this.inputText = "GO TO EXISTING NODE";
        }
        this.inputText = this.inputText === "" ? "Nothing" : this.inputText;
        // this.paraList[0].replace("Story Begin", this.inputTitle);
        // this.paraMindList[0].replace("Story Begin", this.inputTitle);
        // this.mindText.replaceAll('0["Story Begin"]', '0["' + this.inputTitle + '"]')
        this.steps.step1 = true;
        this.steps.step2 = false;
        if (this.inputText.includes('"')) {
          alert("Converted all double quotes to single quotes");
          this.inputText = this.inputText.replaceAll('"', "'");
        }
        this.paragraphs.push({
          sourceIndex: this.paraIndexFrom,
          wordIndex: this.wordIndex,
          content: this.inputText,
          paragraphIndex: this.paraIndexGoTo
        });
        //index
        if (this.paraIndexGoTo > this.index) {
          this.index += 1;
        }
        //
        if (this.paraIndexFrom < this.index) {
          this.mindText += '\n' + this.paraIndexFrom + '["' + this.paraMindList[this.paraIndexFrom] + '"]';
        }
        //mermaid
        //
        this.mindText += this.wordIndex === ' ' ? '--->' : '-->|' + this.wordIndex + '|';
        //
        this.goBack = this.paraIndexGoTo === this.index ? false : true
        this.alpha = this.inputText;
        const textWithLineBreaks = this.splitText(this.alpha, 10);
        this.paraList.push(this.inputText);
        this.paraMindList.push(textWithLineBreaks + ' [' + this.index + ']');
        this.inputText = '[' + this.paraIndexFrom + '] ' + '[' + this.wordIndex + '] ' + this.inputText;

        if (this.goBack) {
          this.mindText += this.paraIndexGoTo + '["' + this.paraMindList[this.paraIndexGoTo] + '"]';
        } else {
          this.mindText += this.index + '["' + textWithLineBreaks + '[' + this.index + ']"]';
        }

        this.para += this.inputText + ' [' + this.paraIndexGoTo + ']' + '\n';
        this.allStory += this.inputText + ' [' + this.paraIndexGoTo + ']' + '\n';
        this.inputText = '';
        this.wordIndex = ' ';
        this.paraIndexFrom = this.paraIndexGoTo;
        this.paraIndexGoTo = this.index + 1;
        if (updateMindMap) {
          this.generateMindMap();
        }

      }

    },
    downloadText() {
      var blob = new Blob(["Story Title: " + this.inputTitle + '\n' + this.allStory], {
        type: "text/plain;charset=utf-8"
      });
      saveAs(blob, this.inputTitle + ".txt");
    },

    handleWordClick() {
      const selection = window.getSelection();
      const range = selection.getRangeAt(0);
      const text = range.toString().trim();

      if (text !== '') {
        // 
        const targetTextNode = range.endContainer;
        //
        const textAfterSelection = targetTextNode.textContent.slice(range.endOffset);
        const nextParaIndexMatch = textAfterSelection.match(/\[(\d+)\]/);

        if (nextParaIndexMatch) {
          const paraIndex = parseInt(nextParaIndexMatch[1], 10);
          this.paraIndexFrom = paraIndex;
        } else {
          //,2
          const whichParagraph = this.paragraphs.find((paragraph) => {
            const contentAlpha = "[" + paragraph.sourceIndex + "] " + "[" + paragraph.wordIndex + "] " + paragraph.content + " [" + paragraph.paragraphIndex + "]"
            return contentAlpha.toLowerCase().includes(targetTextNode.textContent.toLowerCase());
          });
          this.paraIndexFrom = whichParagraph.paragraphIndex;
        }

        const maxLength = 70;
        this.wordIndex = text.substring(0, maxLength);
      }
    },

    async generateMindMap() {
      const mermaidText = `graph TB\n${this.mindText}`;
      // MindMap
      mermaid.initialize({
        flowchart: {
          htmlLabels: false,
          curve: 'linear'
        },
        theme: 'default',
        fontFamily: '"Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif',
        fontSize: 14,
        startOnLoad: true,
        securityLevel: 'loose',
        logLevel: "fatal",
        useMaxWidth: false,
        allowParallelEdges: false,
        marginRight: 50,
        arrowMarkerAbsolute: false,
        nodeSpacing: 50,
        flowstate: {
          active: { fill: 'rgba(130,130,130,0.5)', "font-size": "12px" },
          inactive: { fill: 'rgba(130,130,130,0.5)', "font-size": "12px" },
        },
        useWidth: false,
        padding: 20
      });
      const result = await mermaid.mermaidAPI.render('mindMapSvg', mermaidText);
      const svg = result.svg;
      this.mindMap = svg;
      //  SVG 
      this.$nextTick(() => {
        const svgElement = document.getElementById('mindMapSvg');
        this.attachZoomAndDragHandlers(svgElement);
      });
    },

    attachZoomAndDragHandlers(svgElement) {
      let scale = 1;
      let translateX = svgElement.clientWidth / 2;
      let translateY = svgElement.clientHeight / 2;
      let mouseDown = false;
      let prevMousePos;
      let altKeyPressed = false;
      let shiftKeyPressed = false;
      function updateTransform() {
        svgElement.style.transform = `translate(${translateX}px, ${translateY}px) scale(${scale})`;
      }
      svgElement.addEventListener('wheel', (e) => {
        e.preventDefault();
        if (shiftKeyPressed) {
          translateX += Math.sign(e.deltaY) * 12;
        } else if (altKeyPressed) {
          translateY += Math.sign(e.deltaY) * 12;
        } else {
          const delta = Math.sign(e.deltaY) * -0.3;//
          scale += delta;
          scale = Math.max(0.1, Math.min(20, scale));
        }
        updateTransform();
      });


      svgElement.addEventListener('mousedown', (e) => {
        if (!altKeyPressed) return;
        e.preventDefault();
        mouseDown = true;
        prevMousePos = {
          x: e.clientX,
          y: e.clientY
        };
      });

      svgElement.addEventListener('mousemove', (e) => {
        if (!mouseDown || !altKeyPressed) return;
        e.preventDefault();
        const dx = e.clientX - prevMousePos.x;
        const dy = e.clientY - prevMousePos.y;
        translateX += dx;
        translateY += dy;
        updateTransform();
        prevMousePos = {
          x: e.clientX,
          y: e.clientY
        };
      });

      svgElement.addEventListener('mouseup', (e) => {
        if (!altKeyPressed) return;
        e.preventDefault();
        mouseDown = false;
      });

      svgElement.addEventListener('mouseleave', (e) => {
        if (!altKeyPressed) return;
        e.preventDefault();
        mouseDown = false;
      });

      // 
      window.addEventListener('keydown', (e) => {
        if (e.key === 'Alt') {
          altKeyPressed = true;
        } else if (e.key === 'Shift') {
          shiftKeyPressed = true;
        }
      });

      window.addEventListener('keyup', (e) => {
        if (e.key === 'Alt') {
          altKeyPressed = false;
        } else if (e.key === 'Shift') {
          shiftKeyPressed = false;
        }
      });
      // Set initial scale and translate
      scale = 1; //  2
      translateX = svgElement.clientWidth / 2 * (1 - scale);
      translateY = svgElement.clientHeight / 2 * (1 - scale);
      updateTransform();
    },
    splitText(text, wordsPerLine) {
      const words = text.split(' ');
      let result = '';
      for (let i = 0; i < words.length; i++) {
        result += words[i] + (i % wordsPerLine === wordsPerLine - 1 ? '\n' : ' ');
      }
      return result;
    },
  }
};
</script>

